          <h2 class="main-title">CONTACT</h2>
        </div>
      </div>
    </div>
    <label for="side-menu-toggle" class="side-menu-toggle-label"
    @click="overflowHandler">
    <span class="hamburger"></span>
    <span class="hamburger"></span>
    <span class="hamburger"></span>
  </label>
  </div>
</template>

<script>
export default {
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll)
  },
  data() {
    return {
      hamburger: false,
      view: {
        topOfPage: true
      }
    }
  },
  methods: {
    overflowHandler() {
      if(!this.hamburger) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.removeAttribute("style");
      }
      
    },
    handleScroll(){
      let screenHeight = document.querySelector('.homepage-container').clientHeight
      if(window.pageYOffset >= screenHeight){
        if(this.view.topOfPage) this.view.topOfPage = false
      } else {
        if(!this.view.topOfPage) this.view.topOfPage = true
      }
    }
  }
};
</script>


